import classNames from "classnames";
import * as style from "../assets/scss/index.module.scss";
import React from "react";
import {graphql, useStaticQuery} from "gatsby";
import Tags from "./tags";

const TagList: React.FunctionComponent = () => {
    const data = useStaticQuery(graphql`
        query {
            allMdx (filter: {internal: {type: {eq: "Mdx"}}, frontmatter: {published: {eq: true}}}) {
                distinct(field: frontmatter___tags)
            }
        }
    `)

    return <li className={classNames(style.navItem, style.gMy5)}>
        <h3 className={classNames(style.h5, style.gColorBlack, style.gFontWeight600, style.mb4)}>系列文章</h3>
        <Tags tags={data.allMdx.distinct} />
    </li>
}

export default TagList;